import React from "react";
import L from 'leaflet';
import { Map, 
  TileLayer, 
  Marker, 
  Popup, 
  ZoomControl,
  VideoOverlay } from "react-leaflet";
import Basemap from './Basemaps';
import GeojsonLayer from '../layers/GeojsonLayerFunc';
import VelocityLayer from "../layers/VelocityLayer";
import '../css/Map.css';
import { connect } from "react-redux";

//     marker
L.Icon.Default.imagePath = "https://unpkg.com/leaflet@1.5.0/dist/images/";

class MapComponent extends React.Component {
  state = {
    //       
    lat: 46.536032,  // 
    lng: 41.031736, // 
    zoom: 10,     //     
    basemap: 'osm',
  };

  onBMChange = (bm) => {
    this.setState({
      basemap: bm
    });
  }

  render() {
    // console.log(this.props);
    const layersTypes = {
      'geojson': GeojsonLayer,
      'velocityLayer': VelocityLayer,
      'videoOverlay': VideoOverlay
    }
    let center = [this.state.lat, this.state.lng];

    const basemapsDict = {
      osm: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      hot: "https://{s}.tile.openstreetmap.fr/hot/{z}/{x}/{y}.png",
      dark: "https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}@2x.png",
      cycle: "https://dev.{s}.tile.openstreetmap.fr/cyclosm/{z}/{x}/{y}.png"
    }

    const bounds = [
      [46.5, 40.9], // -  (  )
      [46.6, 41.2]   // -  (  )
    ];

    return (
        <Map
          zoomControl={false}
          zoom={this.state.zoom}
          center={center}
          minZoom={8} //  ,   
          maxZoom={18} //     
          bounds={bounds} //   
          className="map"
          maxBounds={bounds} //     
          maxBoundsVisibilty={true} //   
          dragging={true} //   
        >
            
          <ZoomControl position={'bottomright'} />
          
          <TileLayer
            attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            url={basemapsDict[this.state.basemap]}
          />
          <Basemap basemap={this.state.basemap} onChange={this.onBMChange} />

          {this.props.layers.map( l => {
            if (l.visible) {
              let LayerComp = layersTypes[l.type];
              return (
                <LayerComp key={l.id} {...l.options}/>
              )
            }
          })}

        
          <Marker position={center}>
            <Popup><div>  {this.state.basemap}</div></Popup>
          </Marker>
        </Map>
    );
  }
};

const mapStateToProps = (state) => {
  return {
    layers: state.layers
  };
};

export default connect(mapStateToProps)(MapComponent);